
import { defineComponent, ref, reactive, computed, onMounted } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { useRouter } from 'vue-router'
import { useI18n } from 'vue-i18n'
import { ElForm } from 'element-plus'
import SubTable from '@/components/sub-table'
import { PageInfo, AddressFormModel, FormModel } from '../edit/types/index'
import {
  customerStyleList,
  activeFlag,
  creditList,
  isDefaultList,
  countrytList,
  cityList,
  provinceList
} from '@/constant/option'
export default defineComponent({
  name: 'edit',
  components: {
    SubTable
  },
  setup() {
    const { t } = useI18n()
    const data = ref<any>([])
    const router = useRouter()
    const isDefaultArray = computed(() => isDefaultList)
    const countrytArray = computed(() => countrytList)
    const cityArray = computed(() => cityList)
    const provinceArray = computed(() => provinceList)
    const dialogVisible = ref(false)
    const formModel = ref<FormModel>({})
    const addressFormModel = ref<AddressFormModel>({})
    onMounted(() => {
      console.log(
        router.currentRoute.value.query,
        'router.currentRoute.value.query'
      )

      if (Object.keys(router.currentRoute.value.query).length !== 0) {
        data.value = [
          {
            name: '王小虎',
            phone: 15251909379,
            countryCode: 'Canada',
            provinceCode: 'BC',
            cityCode: 'ac',
            zipCode: '156711',
            addressOne: '12 esct Floor 2',
            addressTwo: '61 Floor 3',
            isDefault: 1
          },
          {
            name: '张三',
            phone: 15251909378,
            countryCode: 'Canada',
            provinceCode: 'BC',
            cityCode: 'ac',
            zipCode: '156711',
            addressOne: '13 esct Floor 2',
            addressTwo: '2 Floor 3',
            isDefault: 0
          }
        ]
      } else {
        data.value = []
      }
      formModel.value = router.currentRoute.value.query
      if (formModel.value.activeFlag) {
        formModel.value.activeFlag = Number(formModel.value.activeFlag)
      }
      if (formModel.value.openCredit) {
        formModel.value.openCredit = Number(formModel.value.openCredit)
      }
      if (formModel.value.type) {
        formModel.value.type = Number(formModel.value.type)
      }
    })
    const searchInfo = () => {
      const params = Object.assign(
        {},
        {
          pageSize: pageInfo.value.pageSize,
          currentPage: pageInfo.value.currentPage
        }
      )
      console.log(params, 'params')
    }
    const handleSizeChange = (val: number) => {
      pageInfo.value.pageSize = val
      searchInfo()
    }
    const handleRow = (rowData: any, label: any) => {
      addressFormRef.value?.resetFields()
      if (label === 'Edit') {
        addressFormModel.value = rowData
        dialogVisible.value = true
      }
      if (label === 'Delete') {
        ElMessageBox.confirm(t('deleteChange'), t('deleteTips'), {
          distinguishCancelAndClose: true,
          confirmButtonText: t('confirm'),
          cancelButtonText: t('cancel'),
          customClass: 'message-box',
          center: true
        })
          .then(() => {
            ElMessage({
              type: 'success',
              message: t('deleteSuccessful')
            })
          })
          .catch((action: any) => {
            ElMessage({
              type: 'info',
              message: action === 'cancel' ? t('deleteFailed') : t('canceled')
            })
          })
      }
    }
    const handleCurrentChange = (val: number) => {
      pageInfo.value.currentPage = val
      searchInfo()
    }
    const openDialog = (status: any) => {
      addressFormRef.value?.resetFields()
      if (status === 'create') {
        addressFormModel.value = {}
        dialogVisible.value = true
      }
    }
    const pageInfo = ref<PageInfo>({
      totalCount: 100,
      pageSizes: [10, 20, 50, 100],
      handleSizeChange: handleSizeChange,
      handleCurrentChange: handleCurrentChange,
      currentPage: 1,
      pageSize: 20
    })
    const subTableConfig = ref({
      title: t('multiAddress'),
      showSelectColumn: false,
      showIndexColumn: true,
      border: false,
      propList: [
        {
          prop: 'isDefault',
          label: t('isDefault'),
          minWidth: '100',
          type: 'select',
          option: isDefaultArray
        },
        {
          prop: 'addressOne',
          label: t('addressOne'),
          minWidth: '100'
        },
        {
          prop: 'addressTwo',
          label: t('addressTwo'),
          minWidth: '100'
        },
        {
          prop: 'zipCode',
          label: t('zipCode'),
          minWidth: '100'
        },
        {
          prop: 'cityCode',
          label: t('city'),
          minWidth: '100'
        },
        {
          prop: 'provinceCode',
          label: t('province'),
          minWidth: '100'
        },
        {
          prop: 'countryCode',
          label: t('country'),
          minWidth: '100'
        },
        {
          prop: 'phone',
          label: t('phone'),
          minWidth: '100'
        },
        {
          prop: 'name',
          label: t('name'),
          minWidth: '100'
        }
      ],
      isOperation: true,
      operation: {
        label: t('operation'),
        width: '150',
        cols: [
          {
            label: t('edit'),
            type: 'primary',
            handleRow: handleRow,
            icon: 'el-icon-edit'
          },
          {
            label: t('delete'),
            type: 'danger',
            handleRow: handleRow,
            icon: 'el-icon-delete'
          }
        ]
      }
    })
    const customerStyleArray = computed(() => customerStyleList)
    const activeFlagArray = computed(() => activeFlag)
    const creditArray = computed(() => creditList)
    const formRef = ref<InstanceType<typeof ElForm>>()
    const addressFormRef = ref<InstanceType<typeof ElForm>>()
    const rules = reactive({
      name: [{ required: true, trigger: 'change' }],
      type: [{ required: true, trigger: 'change' }]
    })
    const addressRules = reactive({
      name: [{ required: true, trigger: 'change' }],
      cityCode: [{ required: true, trigger: 'change' }]
    })
    const cancel = () => {
      formRef.value?.resetFields()
      router.push({ name: 'customerMain', path: '/main/customer/' })
    }
    const submitForm = () => {
      formRef.value?.validate((valid) => {
        console.log(valid, 'vaild')
        if (valid) {
          console.log('pass')
        }
      })
    }
    const saveAddressInfo = () => {
      addressFormRef.value?.validate((valid) => {
        if (valid) {
          dialogVisible.value = false
        }
      })
    }
    return {
      formModel,
      addressRules,
      rules,
      cancel,
      submitForm,
      formRef,
      customerStyleArray,
      activeFlagArray,
      creditArray,
      t,
      subTableConfig,
      SubTable,
      handleRow,
      data,
      isDefaultArray,
      dialogVisible,
      addressFormModel,
      openDialog,
      countrytArray,
      cityArray,
      provinceArray,
      ElMessage,
      pageInfo,
      searchInfo,
      saveAddressInfo,
      addressFormRef
    }
  }
})
